@use 'sass:math';
@use '../../foundation/theme' as *;
@use '../../mixin' as *;

.c-splide {}

.splide {
  &__pagination {
    position: relative;
    bottom: unset;
    padding: unset;

    li {
      display: flex;
      line-height: unset;
    }

    &__page {
      margin: 0;

      &.is-active {
        transform: unset;
      }
    }
  }

  &__arrows {}

  &__arrow {
    position: relative;

    &--prev {
      left: unset;
    }

    &--next {
      right: unset;
    }
  }
}