@use 'sass:math';
@use '../../foundation/theme' as *;
@use '../../mixin' as *;
/**
 * @file _grid.scss
 * @layer Project
 * @description: 
 * - 
 * 
 * @dependencies: 
 * - ../_mixin.scss
 * 
 * @modifies: 
 * - 
 * 
 * @note: 
 * - 
 */

.p-grid {
    &__content {
        grid-area: content;
    }

    &__media {
        grid-area: media;
    }
    
    &__label {
        grid-area: label;
    }
    
    &__value {
        grid-area: value;
    }
}